.dropdownmenu-button {
  background: transparent;
  border: none;
  cursor: pointer;
  transition: 0.3s;
}
.dropdownmenu-button--active {
  background: transparent;
  border: none;
  cursor: pointer;
  transform: rotate(-90deg);
  transition: 0.3s;
}
.dropdownmenu-box {
  z-index: 20;
  position: absolute;
  display: flex;
  flex-direction: column;
  width:6vw;
  background-color: white;
  border: 2px solid rgb(82, 82, 82);
}

.dropdownmenu-link {
  padding: 0.2vh;
  text-decoration: none;
  color: black;
  padding-left: 0.4vh;
  text-align: left;
}
.dropdownmenu-link:hover {
  background-color: gray;
}

