@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.add-customer-link {
  background-color: #2f9e44;
  text-decoration: none;
  width: 7vw;
  margin: 1vh;
  line-height: 5vh;
  border-radius: 1.5vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: black;
  transition: background-color 0.2s;
}

.add-customer-link:hover {
  background-color: #2b8a3e;
  transition: background-color 0.2s;
}


.search-input {
  overflow: hidden;
  height: 4.5vh;
  border-radius: 1.5vh;
  border-width: 1px;
  margin: 1vh;
  padding-left: 5px;
}

.top-container {
  align-self: flex-end;
  display: flex;
  align-items: flex-end;
  margin-right: 2.5vw;
}

.buttons-container {
  display: flex;
  margin-top: 2vh;
  flex-direction: row;
  align-items: center;
}

.page-number{
  width: 2vw;
}



