.add-form {
  align-self: center;
  display: flex;
  flex-direction: column;
  width: 30vw;
}

.form-group {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.text {
  overflow: hidden;
  white-space: nowrap;
  align-self: flex-start;
  margin-left: 3vw;
}

.add-input {
  overflow: hidden;
  height: 4vh;
  border-radius: 1.5vh;
  border-width: 1px;
  padding-left: 0.5vw;
  width: 24vw;
  margin: 1vh;
}

.input-required {
  border: solid 1px red;
  background-color: #FEE2E2;
}

.button-container {
  width: 30vw;
  display: flex;
  flex-direction: row;
  margin-top: 1vh;
  justify-content: center;
  align-self: center;
}

.back-link {
  background-color:#868e96 ;
  text-decoration: none;
  color: black;
  width: 7vw;
  margin: 1vh;
  line-height: 5vh;
  border-radius: 1.5vh;
}

.back-link:hover {
  background-color: #9CA3AF;
}

.success-message{
  width: 20vw;
  height: 5vh;
  font-size: 2.5vh;
 margin-bottom: 2vh;
}
