* {
  margin: 0;
  padding: 0;
  font-size: 1.3vw;
  font-family: 'Lato', sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  background-color: #272727;
}



section {
  margin: 2vh auto;
  width: 75vw;
  padding: 4vh;
  border-radius: 2vh;
  text-align: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


